<template>
  <!--  -->
  <div class="l_detail__container">
    <div class="l_detail--main c_hero__top__container mb-100">
      <p v-if="isBuy" class="l_detail--main--attention u_font__Xlage--text--attention">SOLD OUT</p>
      <img class="l_detail--main--hero m-auto u_img__fit--cover-detail" :src="image" alt />
      <!-- opacity.3 -->
      <!--  -->
      <!-- img hero -->

      <div class="l_detail--main--info">
        <!--  -->
        <div class="l_detail--main--info--head">
          <div class="l_detail--main--info--right u_display--center">
            <a
              data-hashtags="masizime"
              data-via="masizime"
              data-related="masizime:"
              data-size="large"
              data-text=""
              href="https://twitter.com/share?ref_src=twsrc%5Etfw"
              class="btn--blue btn--sns"
              data-show-count="false"
            >
              <i class="fab fa-twitter fa-2x"></i>
            </a>
          </div>
        </div>
        <div class="l_detail--main--info--info l_detail--main--info__container">
          <h2
            class="l_detail--main--info__container--title u_display--Jstart--info u_font__lage--text"
          >{{ productdata.name }}</h2>

          <p class="l_detail--main--info__container--category u_display--Jstart">
            <span class="btn--mini btn--beige">{{ productdata.category }}</span>
          </p>

          <p
            class="l_detail--main--info__container--expiration u_display--Jstart u_font__text--expiration"
          >{{ productdata.sellby }}</p>
          <p
            class="l_detail--main--info__container--price-l u_display--Jstart u_font__text--price"
          >  {{ productdata.regular_price }}</p>
          <h3
            class="l_detail--main--info__container--price-r u_display--center u_font__text--price--lage u_red--text"
          >  {{ productdata.price }}</h3>
        </div>
        <div class="l_detail--main--info--foot">
          <button
            :class="changeClassBtn"
            v-if="isPurchase"
            @click="clickDone"
            :disabled="isDisabled"
          >{{ toggleText }}</button>
          <!-- if -->
          <component :is="isWhich"></component>
        </div>
      </div>
    </div>
    <div class="l_detail--info u_font__default">
      <!--  -->
      <h1 class="l_detail--info--head u_display--Jstart"></h1>

      <h3 class="l_detail--info--textA u_display--Jstart"></h3>
      <p class="l_detail--info--dataA u_display--Jstart">{{ storedata.name }}</p>

      <h3 class="l_detail--info--textB u_display--Jstart"></h3>
      <p class="l_detail--info--dataB u_display--Jstart">{{ storedata.branch_name }}</p>

      <h3 class="l_detail--info--textC u_display--Jstart"></h3>
      <p class="l_detail--info--dataC u_display--Jstart">{{ storedata.manager_name }}</p>

      <h3 class="l_detail--info--textE u_display--Jstart"></h3>
      <p class="l_detail--info--dataE u_display--Jstart"> {{ storedata.zip }}</p>

      <h3 class="l_detail--info--textF u_display--Jstart"></h3>
      <p class="l_detail--info--dataF u_display--Jstart">{{ storedata.prefectural }}</p>

      <p class="l_detail--info--dataG u_display--Jstart">{{ storedata.address }}</p>

      <h3 class="l_detail--info--textH u_display--Jstart">TEL</h3>
      <p class="l_detail--info--dataH u_display--Jstart">{{ storedata.tel }}</p>
    </div>
  </div>
</template>

<script>
import axios from "axios";
import b_CompoA from "./buyComponentA";
import b_CompoB from "./buyComponentB";
export default {
  props: ["productdata", "storedata", "buydata", "authdata"],
  data() {
    return {
      image: "/storage/" + this.productdata.pic,
      isBuy: false,
      isBuyer: false,
      isDone: false,
      isWhich: "b_CompoA",
      isPurchase: true,
      isDisabled: false,
      tweetView_link: "/tweet/"
    };
  },
  methods: {
    doBuy() {
      axios
        .post("/buy", {
          p_id: this.productdata.id,
          s_id: this.storedata.id,
          u_id: this.authdata
        })
        .then(res => {
          console.log("" + res);
        })
        .catch(error => {
          console.log("" + error);
        });
    },
    clickDone() {
      const res = window.confirm("");
      if (res) {
        //
        if (!this.isBuy) {
          //
          this.isDone = false;
          this.isBuy = true;
          this.doBuy();
          this.isDisabled = true;
          this.judgComponent();
        } else {
          //
          this.isDone = true;
          this.isBuy = false;
          this.doBuy();
          this.isDisabled = true;
          this.judgComponent();
        }
      }
    },
    judgBuyer() {
      //IDbuydataID
      if (this.buydata != null) {
        if (this.authdata === this.buydata.buy_user_id) {
          this.isBuyer = true;
        } else {
          this.isBuyer = false;
        }
      }
    },
    judgBuy() {
      if (this.buydata != null) {
        this.isBuy = true;
        if (this.isBuyer) {
          this.isDisabled = false;
        }
      }
    },
    judgPurchase() {
      if (this.isBuy && this.isBuyer) {
        this.isPurchase = true;
      } else if (!this.isBuy && !this.isBuyer) {
        this.isPurchase = true;
      } else {
        this.isPurchase = false;
      }
    },
    iniSet() {
      this.judgBuyer();
      this.judgBuy();
      this.judgPurchase();
      this.judgComponent();
    },
    showTweetView() {
      //
      window.location.href = this.tweetView_link;
    },
    judgComponent() {
      if (this.isBuy) {
        this.isWhich = "b_CompoB";
        return;
      } else {
        this.isWhich = "b_CompoA";
        return;
      }
    }
  },
  computed: {
    twitter_Link() {
      return "/twitter/" + this.productdata.name + "/" + this.image;
    },
    changeClassBtn() {
      return {
        btn: true,
        "btn--transition": true,
        "l_detail--main--info--btn": true,
        "btn--blue": !this.isBuy,
        "btn--red": this.isBuy
      };
    },
    toggleText() {
      if (this.isBuy) {
        return "";
      } else {
        return "";
      }
    }
  },
  beforeMount() {
    console.log("mounted");
    this.iniSet();
  },
  components: {
    b_CompoA,
    b_CompoB
  }
};
</script>
